@import '../../mixin.scss';

.cartItem {
  position: relative;
  padding: 30px 40px 25px;

  &__delete:hover {
    border: none;
  }

  &__image {
    width: 65px;
    height: 65px;
    margin: 0 25px;
  }

  &__name {
    @include Body-text-14;
    color: #313237;
  }

  &__change-price-box,
  &__itemsPrice {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &__change-price-box {
    display: inline-block;
    right: 140px;
  }

  &__itemsPrice {
    right: 40px;
  }

  &__button {
    width: 30px;
    height: 30px;
    font-size: 15px;
    border: 1px solid #E2E6E9;
  }

  &__button--plus {
    margin-right: 40px;
  }

  &__button--disabled {
    color: #E2E6E9;
  }

  &__button--disabled:hover {
    border: 1px solid #E2E6E9;
  }

  &__item-count {
    @include Body-text-14;
    color: #000;
    margin: 0 12px;
  }
}

